.primer-div {
    color: white;
    background-color: black;
    display: flex;
    justify-content: space-around;
    align-items: center;

    @media (max-width: 768px) {
        flex-direction: column;
    }
}

.home-title {
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 65pt;

    @media (max-width: 768px) {
        width: 90%;
        font-size: 40pt;
        text-align: center;
    }
}

.card-3d {
    height: 80vh;
    width: 50%;

    @media (max-width: 768px) {
        width: 90%;
    }
}

